/* Color Variables */

:root {
    --one: #e24e42;
    --two: #d9b310;
    --three: #eb6e80;
    --four: #008f95;
    --five: #2b2a2a;
}

/* Media queries */

@media only screen and (max-width: 360px) {
    .trends {
        padding: 10px;
    }
    .ranked-langs {
        padding-bottom: 1rem !important;
    }
}

body {
    background-color: rgb(255, 255, 255);
    background-image: url(/coding_challenge-13/static/media/parallax.4cbbd78c.svg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Roboto Mono', monospace;
}

p,
label {
    font-family: 'Comfortaa', cursive;
}

body::-webkit-scrollbar {
    width: 15px;
}

body::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
    -webkit-box-shadow: inset 0 0 6px rgba(231, 47, 1, 0.651);
}

body::-webkit-scrollbar-thumb {
    background-color: #e24e42;
    background-color: var(--one);
    outline: 1px solid #e24e42;
    outline: 1px solid var(--one);
}

.title {
    font-family: 'Raleway', sans-serif;
    margin-bottom: 25px;
}

.trends {
    width: 80vw;
    margin: 30px auto;
    padding: 50px 30px;
    text-align: center;
    background: #f0d6c886;
    border: 1px solid #fff;
    border-radius: 30px;
}

.chart-container {
    position: relative;
    width: 95%;
    margin: 0 auto auto auto;
}

h2 {
    color: #e24e42;
    color: var(--one);
    display: inline-flex;
    border-bottom: 2px solid #e24e42;
    border-bottom: 2px solid var(--one);
    padding-bottom: 2px;
}

.svg-flip {
    position: relative;
    top: -1px;
    -webkit-transform: rotate(180deg);
    /* Chrome and other webkit browsers */
    /* FF */
    /* Opera */
    /* IE9 */
    transform: rotate(180deg);
    z-index: -1;
}

.chartHolder {
    display: grid;
    grid-template-areas: "chart switchbox" "chart zoombox";
    grid-template-columns: 90% 10%;
    align-items: center;
}

.switchbox {
    grid-area: switchbox;
    margin-left: -150px;
    width: 200px;
    align-self: flex-end;
}

.chartbox {
    grid-area: chart;
    width: 60vw;
    justify-self: end
}

.chartbox .legend {
    display: none;
    grid-template-columns: 1fr 1fr 1fr 1fr
}

.chartbox .legend>span {
    color: black;
    font-size: 1em;
}

.chartbox .legend>span>div {
    display: inline-block;
    width: 12px;
    height: 12px;
    margin: 0 5px
}

@media (hover:none) {
    .chartbox .legend {
        display: grid;
    }
}

.zoombox {
    grid-area: zoombox;
    justify-self: start;
    margin-left: -150px;
    width: 200px;
    align-self: flex-start;
    z-index: 1;
}

@media only screen and (max-width: 1200px) {
    .chartHolder {
        grid-template-areas: "chart chart" "switchbox zoombox";
        grid-template-columns: 50% 50%;
    }
    .chartbox {
        justify-self: center
    }
    .chartbox .legend {
        grid-template-columns: 1fr 1fr;
        justify-items: start;
    }
    .chartbox .legend>span {
        font-size: .7em;
    }
    .chartbox .legend>span:first-child {
        justify-self: end;
    }
    .chartbox .legend>span:nth-child(3) {
        justify-self: end;
    }
    .switchbox {
        margin-left: 0;
        justify-self: center
    }
    .zoombox {
        justify-self: center;
        margin-left: 0;
    }
}

@media only screen and (max-width: 550px) {
    .chartHolder {
        grid-template-areas: "chart chart" "zoombox zoombox" "switchbox switchbox";
        grid-template-columns: 50% 50%;
    }
    .chartbox {
        justify-self: center;
    }
    .chartbox .legend {
        grid-template-columns: 1fr;
        justify-items: center;
    }
    .chartbox .legend>span:first-child {
        justify-self: center;
    }
    .chartbox .legend>span:nth-child(3) {
        justify-self: center;
    }
    .switchbox {
        margin-left: 0;
        justify-self: center
    }
    .zoombox {
        justify-self: center;
        margin-left: 0;
    }
}

