.tooltip1 {
    display: inline-block;
    position: relative;
    text-align: left;
}

.tooltip1 .top {
    min-width: 200px;
    top: -20px;
    left: 50%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    padding: 10px 0 5px 0;
    color: var(--three);
    background-color: rgba(255, 255, 255, 0.95);
    font-weight: normal;
    font-size: 13px;
    border-radius: 8px;
    position: absolute;
    z-index: 99999999;
    box-sizing: border-box;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
    display: none;
    border: 1px solid var(--three);
    text-transform: uppercase;
    -webkit-animation: pop 0.2s;
            animation: pop 0.2s;
}

@-webkit-keyframes pop {
    0% {
        -webkit-transform: translate(-50%, -100%) scale(0);
                transform: translate(-50%, -100%) scale(0);
    }
    100% {
        -webkit-transform: translate(-50%, -100%) scale(1);
                transform: translate(-50%, -100%) scale(1);
    }
}

@keyframes pop {
    0% {
        -webkit-transform: translate(-50%, -100%) scale(0);
                transform: translate(-50%, -100%) scale(0);
    }
    100% {
        -webkit-transform: translate(-50%, -100%) scale(1);
                transform: translate(-50%, -100%) scale(1);
    }
}

.tooltip1:hover .top {
    display: block;
    -webkit-transform: translate(-50%, -100%) scale(1);
            transform: translate(-50%, -100%) scale(1);
}

.tooltip1 .top i {
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -12px;
    width: 24px;
    height: 12px;
    overflow: hidden;
}

.tooltip1 .top i::after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    background-color: var(--three);
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
}

/* Animated Arrow */

.arrow-down {
    width: 3rem;
    height: 3rem;
    border: 0.2rem solid;
    border-color: white transparent transparent white;
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}

.arrow-right {
    width: 2.5rem;
    height: 2.5rem;
    border: 0.2rem solid;
    border-color: white transparent transparent white;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

.arrowSlideDown {
    position: absolute;
    -webkit-animation: slide-down 2s linear infinite;
    animation: slide-down 2s linear infinite;
}

.arrowSlideRight {
    position: absolute;
    -webkit-animation: slide-right 2s linear infinite;
    animation: slide-right 2s linear infinite;
}

.delay1 {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

.delay2 {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
}

.delay3 {
    -webkit-animation-delay: 1.5s;
    animation-delay: 1.5s;
}

@-webkit-keyframes slide-down {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-3rem);
                transform: translateY(-3rem);
    }
    20% {
        opacity: 1;
        -webkit-transform: translateY(-2rem);
                transform: translateY(-2rem);
    }
    80% {
        opacity: 1;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-3rem);
                transform: translateY(-3rem);
    }
    20% {
        opacity: 1;
        -webkit-transform: translateY(-2rem);
                transform: translateY(-2rem);
    }
    80% {
        opacity: 1;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
}

@-webkit-keyframes slide-right {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-4rem);
                transform: translateX(-4rem);
    }
    20% {
        opacity: 1;
        -webkit-transform: translateX(-2rem);
                transform: translateX(-2rem);
    }
    80% {
        opacity: 1;
        -webkit-transform: translateX(2rem);
                transform: translateX(2rem);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(4rem);
                transform: translateX(4rem);
    }
}

@keyframes slide-right {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-4rem);
                transform: translateX(-4rem);
    }
    20% {
        opacity: 1;
        -webkit-transform: translateX(-2rem);
                transform: translateX(-2rem);
    }
    80% {
        opacity: 1;
        -webkit-transform: translateX(2rem);
                transform: translateX(2rem);
    }
    100% {
        opacity: 0;
        -webkit-transform: translateX(4rem);
                transform: translateX(4rem);
    }
}

/* originally authored by Nick Pettit - https://github.com/nickpettit/glide */

@-webkit-keyframes pulse {
    from {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
    50% {
        -webkit-transform: scale3d(1.05, 1.05, 1.05);
        transform: scale3d(1.05, 1.05, 1.05);
    }
    to {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

@keyframes pulse {
    from {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
    50% {
        -webkit-transform: scale3d(1.05, 1.05, 1.05);
        transform: scale3d(1.05, 1.05, 1.05);
    }
    to {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

.pulse {
    -webkit-animation-name: pulse;
    animation-name: pulse;
}

.animated {
    -webkit-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.animated.infinite {
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}

